<template>
  <div>
    <!-- 数据列表 - 筛选-->
    <el-card class="operate-container mt10" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="客户名称：">
            <el-input v-model="searchList.nickName" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="开通/续费时间：">
            <el-date-picker
              v-model="searchList.time"
              type="datetimerange"
              :picker-options="pickerOptions"
              value-format="yyyy-MM-dd HH:mm:ss"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>

            <!-- <el-date-picker v-model="searchList.time"
              type="daterange"
              value-format="yyyy-MM-dd HH:mm:ss"
              :picker-options="pickerOptions"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker> -->
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" tooltip-effect="dark" style="width: 100%">
          <el-table-column label="客户名称" width="200" prop="nickName"></el-table-column>
          <el-table-column label="手机号" width="200" prop="cellPhone"></el-table-column>
          <el-table-column label="送会员操作人" width="200" prop="operateUserName"></el-table-column>
          <el-table-column label="开通/续费时间" width="200" prop="startTime"></el-table-column>
          <el-table-column label="支付金额" width="200">
            <template slot-scope="scope">
              {{scope.row.amount ? scope.row.amount : '-'}}
            </template>
          </el-table-column>
          <el-table-column label="有效期" width="200">
            <template slot-scope="scope">
              {{scope.row.effectiveTime ? scope.row.effectiveTime : '-'}}
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :page-sizes="[5,10,15,20]"
            :page-size="searchList.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="listTotal">
          </el-pagination>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { userVipRecordList } from '@/api/memberManage/memberCenter'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了记录')
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1,
        time:[]
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime()- 3600 * 1000 * 24);
              end.setHours(23);
              end.setMinutes(59);
              end.setSeconds(59);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
      list:[],
      listTotal:0
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.getData()
  },
  methods:{
    getData(){
      this.searchList.startTime = this.searchList.time[0] || ''
      this.searchList.endTime = this.searchList.time[1] || ''
      userVipRecordList(this.searchList).then(res=>{
        console.log('会员开通记录列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.nickName = ''
      this.searchList.time = []
      this.getData()
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>