<template>
  <div class="pageContent">
    <el-tabs v-model="activeName" class="headtab">
      <el-tab-pane label="会员中心" name="0">
        <center :getPost="activeName == 0"></center>
      </el-tab-pane>
      <el-tab-pane label="开通记录" name="1">
        <note :getPost="activeName == 1"></note>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import center from './components/center';
import note from './components/note';
import { mapState } from 'vuex'
export default {
  data(){
    return{
      activeName:0
    }
  },
  computed: {
    ...mapState([
      'shop',
      'user'
    ]),
  },
  components: {
    center,
    note
  },
  created(){
    if(this.$route.query.type) this.activeName = this.$route.query.type + ''
  },
}
</script>
<style lang="less" scoped>
.headtab /deep/ .is-top{
  margin-bottom: 0;
}
</style>